'use client'
import { useState, useEffect } from "react"
import styles from "./Post.module.css"
import React from 'react'
import Slider from "react-slick";

type Pizza = {
    id: number,
    nome: string,
    descricao: string,
    preco: number,
    imagem: string
}

export default function Post() {
    const [pizzas, setPizzas] = useState<Pizza[]>([]);
    const [imagem, setImagem] = useState("");
    const [nome, setNome] = useState("");
    const [descricao, setDescricao] = useState("");
    const [preco, setPreco] = useState("");


    useEffect(() => {
        fetch("http://localhost:3000/cardapio")
            .then((res) => res.json())
            .then((data) => setPizzas(data));
    }, [])

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
    };

    return (
        <>

            <Slider {...settings}>
                <section className={styles.post}>


                    {pizzas.map((pizza) => (
                        <div key={pizza.id} className={styles.postPizza}>
                            <img src={`http://localhost:3000${pizza.imagem}`} alt="" />
                            <h1>{pizza.nome}</h1>
                            <p>{pizza.descricao}</p>
                            <h2>{pizza.preco}</h2>
                            <button type="submit"><h1>Pedir agora</h1></button>
                        </div>
                    ))}



                </section>
            </Slider>

        </>
    )
}